.parallaxContainer{
    width:100%;
    height:100vh;
    display:flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;


    h1{
        z-index:3;
        letter-spacing: 5px;
    }
    .beach{
        position: absolute;
        width:100%;
        height:100%;
       
        opacity:0.5;
        img{
            width:100%;
            height:100%;
        }
    }
    .stars{
        width:100%;
        height:70%;
        position: absolute;
        top:0;
        img{
            width:100%;
            height:100%;
        }
        
    }

    .mountain   {
        z-index:100;
        width:100%;
        height:100%;
        position: absolute;
        top:0;
        img{
            width:100%;
            height:100%;
        }
    }

}